import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import routeConstant from 'routes/route/routeConstant';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnCancel, BtnDelete, BtnAddNew } from 'components/common/button';
import { Row, Card, Col, Form, Input, Checkbox, message } from 'antd';
import {
  YearSemesterInput,
  SelectAjax,
  DatePickerString,
  TableDataEditCell,
  InputMoney,
} from 'components/common';
import { requiredRule } from 'utils/validateRules';
import { getPrizeClassification } from 'services/prizeTypeForAddScholarship';
import { getStudentCode, createprizeNotes, getPrizeClassifiedAmount } from 'services/addPrizeNote';

@Form.create({ name: 'formAddScholarship' })
class FormAddScholarship extends Component {
  state = {
    initialValue: {},
    dataAddNewScholarship: [],
    loading: false,
    reload: 0,
  };

  componentDidMount = () => {};

  onChangeCode = (val, index) => {
    const { value } = val.target;
    this.getInputCode(value, index);
  };

  onChangePrizeClassification = async(val, key) => {
    const { dataAddNewScholarship } = this.state;
    const itemByKey = dataAddNewScholarship.find(element => element.key === key);
    let data = dataAddNewScholarship.map((item) => {
      if (item.key === key) {
        item.solutionId = val;
      }
      return item;
    });
    if(itemByKey.code) {
      const tempAmount = await getPrizeClassifiedAmount({
        studentId: itemByKey.studentId,
        prizeClassId: val,
      });
      data = data.map((item) => {
        if (item.key === key) {
          item.amount = tempAmount.data.amount || 0;
        }
        return item;
      });
    }
    this.setState({ dataAddNewScholarship: [...data], reload: this.state.reload + 1 });
  };

  getInputCode = async (val, index) => {
    try {
      const { dataAddNewScholarship } = this.state;
      const { value } = val.target;
      if (value.trim().length === 0) {
        const data = dataAddNewScholarship.map((item, indexItem) => {
          if (indexItem === index) {
            item.key = dataAddNewScholarship[index].key;
            item.code = '';
            item.studentId = '';
            item.fullname = '';
            item.dob = '';
            item.className = '';
            item.solutionLabel = dataAddNewScholarship[index].solutionLabel;
            item.solutionId = dataAddNewScholarship[index].solutionId;
            item.amount = dataAddNewScholarship[index].amount;
            item.isCorp = dataAddNewScholarship[index].isCorp;
          }
          return {
            key: item.key,
            code: item.code,
            studentId: item.studentId,
            fullname: item.fullname,
            dob: item.dob,
            className: item.className,
            solutionLabel: item.solutionLabel,
            solutionId: item.solutionId,
            amount: item.amount,
            isCorp: item.isCorp,
          };
        });
        this.setState({ dataAddNewScholarship: data, reload: this.state.reload + 1 });
      }
      if (value.trim().length > 0) {
        let dataStudentCode = '';
        try {
          dataStudentCode = await getStudentCode(value);
        } catch (err) {
          const { status } = JSON.parse(JSON.stringify(err)).response;
          if (status === 404) {
            message.warning('MSSV không tồn tại!');
          }
        }
        const data = dataAddNewScholarship.map((item, indexItem) => {
          if (indexItem === index) {
            item.key = dataAddNewScholarship[index].key;
            item.code = dataStudentCode.data.code;
            item.studentId = dataStudentCode.data.id;
            item.fullname = `${dataStudentCode.data.firstname} ${dataStudentCode.data.lastname}`;
            item.dob = dataStudentCode.data.dob;
            item.className = dataStudentCode.data.className;
            item.solutionLabel = dataAddNewScholarship[index].solutionLabel;
            item.solutionId = dataAddNewScholarship[index].solutionId;
            item.amount = dataAddNewScholarship[index].amount;
            item.isCorp = dataAddNewScholarship[index].isCorp;
          }
          return {
            key: item.key,
            code: item.code,
            studentId: item.studentId,
            fullname: item.fullname,
            dob: item.dob,
            className: item.className,
            solutionLabel: item.solutionLabel,
            solutionId: item.solutionId,
            amount: item.amount,
            isCorp: item.isCorp,
          };
        });
        this.setState({ dataAddNewScholarship: data, reload: this.state.reload + 1 });
      }
    } catch (err) {
      // console.log(err);
    }
  };

  getDataAddToTable = () => ({ data: { items: this.state.dataAddNewScholarship } });

  addRowDataTable = () => {
    const { dataAddNewScholarship } = this.state;
    const item = {
      key: new Date().getTime(),
      code: '',
      studentId: '',
      fullname: '',
      dob: '',
      className: '',
    };
    dataAddNewScholarship.push(item);
    this.setState({
      dataAddNewScholarship,
      reload: this.state.reload + 1,
    });
  };

  deleteRowDataTable = async key => {
    const { dataAddNewScholarship } = this.state;
    const data = dataAddNewScholarship.filter(item => item.key !== key);
    this.setState({
      dataAddNewScholarship: data,
      reload: this.state.reload + 1,
    });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields(async (err, values) => {
      if (err) {
        return;
      }
      try {
        const { dataAddNewScholarship } = this.state;
        this.setState({ loading: true });

        const dataDetailsRow = dataAddNewScholarship.map(item => {
          const data = {
            studentId: item.studentId,
            solutionId: item.solutionId,
            amount: item.amount,
            isCorp: item.isCorp,
          };

          return data;
        });

        if (dataDetailsRow.length === 0) {
          message.warning('Vui lòng nhập học bổng!');
        }

        const dataDetailsRowFilterEmpty = dataDetailsRow.filter(
          item =>
            item.studentId !== '' &&
            item.studentId !== undefined &&
            item.solutionId !== '' &&
            item.solutionId !== undefined &&
            item.amount !== '' &&
            item.amount !== undefined
        );

        if (dataDetailsRow.length - dataDetailsRowFilterEmpty.length > 0) {
          message.warning('Vui lòng nhập cả học bổng và MSSV!');
        }

        if (
          dataDetailsRow.length > 0 &&
          dataDetailsRow.length - dataDetailsRowFilterEmpty.length === 0
        ) {
          const dataCreate = { id: null, ...values, pnDetails: dataDetailsRow };
          await createprizeNotes(dataCreate);
          this.props.history.push(routeConstant.HOC_BONG__NHAP_QUYET_DINH);
        }
      } finally {
        this.setState({ loading: false });
      }
    });
  };

  updateDataRow = async (key, dataRow) => {
    const { dataAddNewScholarship } = this.state;
    const indexRow = dataAddNewScholarship.findIndex(item => item.key === key);
    const tempData = [...dataAddNewScholarship];
    tempData[indexRow] = dataRow;
    this.setState({
      dataAddNewScholarship: tempData,
      reload: this.state.reload + 1,
    });
  };

  handleSaveCellData = async (rowData, { error }) => {
    const { solutionId, amount, isCorp = false } = rowData;

    const newEmptyDataRow = {
      ...rowData,
      solutionId: '',
      amount: '',
      isCorp,
    };

    if (!solutionId || !amount) {
      message.warning('Vui lòng nhập học bổng!');
      await this.updateDataRow(rowData.key, newEmptyDataRow);
    }

    const newDataRow = {
      ...rowData,
      solutionId,
      amount,
      isCorp,
    };

    // console.log('newDataRow', newDataRow);
    if (error === null) {
      await this.updateDataRow(rowData.key, newDataRow);
    }
    return newDataRow;
  };

  render() {
    const { initialValue, loading } = this.state;
    const { getFieldDecorator } = this.props.form;

    const columnsCell = [
      {
        title: 'Mã sinh viên',
        dataIndex: 'code',
        key: 'code',
        render: (text, record, index) => (
          <Input
            placeholder="Nhập MSSV"
            allowClear
            defaultValue={record.code}
            maxLength={10}
            onChange={e => this.onChangeCode(e, index)}
            onBlur={e => this.getInputCode(e, index)}
          />
        ),
      },
      {
        title: 'Họ tên',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'dob',
        key: 'dob',
      },
      {
        title: 'Lớp',
        dataIndex: 'className',
        key: 'className',
      },
      {
        title: 'Loại học bổng',
        dataIndex: 'solutionLabel',
        width: '150px',
        key: 'solutionId',
        editable: true,
        alwayShowEdit: true,
        renderFormItemEdit: ({ form, record, setRefFormItem }) => (
          <Form.Item>
            {form.getFieldDecorator(`${record.key}.solutionId`, {
              initialValue: record.solutionId,
              rules: [requiredRule],
            })(
              <SelectAjax
                form={form}
                service={getPrizeClassification}
                allowGetObjSelected
                ref={setRefFormItem}
                onChange={e => this.onChangePrizeClassification(e, record.key)}
              />
            )}
          </Form.Item>
        ),
      },
      {
        title: 'Số tiền',
        dataIndex: 'amount',
        key: 'amount',
        editable: true,
        alwayShowEdit: true,
        renderFormItemEdit: ({ form, record, setRefFormItem }) => (
          <Form.Item>
            {form.getFieldDecorator(`${record.key}.amount`, {
              initialValue: record.amount,
              rules: [requiredRule],
            })(
              <InputMoney
                ref={setRefFormItem}
                style={{ width: '100%' }}
              />
            )}
          </Form.Item>
        ),
      },
      {
        title: 'HB DNghiệp',
        dataIndex: 'isCorp',
        key: 'isCorp',
        editable: true,
        alwayShowEdit: true,
        renderFormItemEdit: ({ form, record, setRefFormItem }) => (
          <Form.Item>
            {form.getFieldDecorator(`${record.key}.isCorp`, {
              valuePropName: 'checked',
              initialValue: record.isCorp,
            })(<Checkbox ref={setRefFormItem} />)}
          </Form.Item>
        ),
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        key: 'operation',
        render: (text, record) => <BtnDelete onClick={() => this.deleteRowDataTable(record.key)} />,
      },
    ];
    return (
      <Card>
        <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSubmit}>
          <Row gutter={24}>
            <Col span={6}>
              <Form.Item label="Số QĐ" hasFeedback>
                {getFieldDecorator('approvalNumber', {
                  initialValue: initialValue.approvalNumber,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input />)}
              </Form.Item>
            </Col>
            <Col span={6}>
              <Form.Item label="Ngày QĐ" hasFeedback>
                {getFieldDecorator('approvalDate', {
                  initialValue: initialValue.approvalDate,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<DatePickerString placeholder="Chọn ngày" />)}
              </Form.Item>
            </Col>
            <Col span={6}>
              <Form.Item label="Nội dung" hasFeedback>
                {getFieldDecorator('description', {
                  initialValue: initialValue.description,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input />)}
              </Form.Item>
            </Col>
            <YearSemesterInput
              form={this.props.form}
              optionFieldDecorator={{
                yearEduId: {
                  rules: [requiredRule],
                },
                semesterId: {
                  rules: [requiredRule],
                },
              }}
            />
            <Col span={6}>
              <div className="btn-group">
                <BtnSave htmlType="submit" loading={loading} />
                <BtnCancel onClick={() => this.props.history.goBack()} />
              </div>
            </Col>
          </Row>
        </Form>
        <Row>
          <BtnAddNew title="Thêm học bổng" onClick={this.addRowDataTable} />
        </Row>
        <Row>
          <Col span={24}>
            <TableDataEditCell
              reload={this.state.reload}
              paramSearch={this.state.paramSearch}
              service={this.getDataAddToTable}
              columns={columnsCell}
              handleSaveCellData={this.handleSaveCellData}
              rowKey="key"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormAddScholarship {...props} />
  </Page>
));



