import { request, requestDownload } from 'utils';

const prefix = '/api/students';

export const getPrizeNotes = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  semesterId,
  yearEduId,
  studentCode,
  studentFullName,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
}) =>
  request({
    prefix,
    url: '/PrizeNote/GetPrizeNoteStudents',
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      semesterId,
      yearEduId,
      studentCode,
      studentFullName,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const deletePrizeNotes = id =>
  request({
    prefix,
    url: `/PrizeNote/${id}`,
    method: 'POST',
  });

export const createprizeNotes = data =>
  request({
    prefix,
    url: `/PrizeNote/CreatePNNoteAndDetail`,
    data,
    method: 'POST',
  });

export const exportPrizeNotes = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  semesterId,
  yearEduId,
  studentCode,
  studentFullName,
}) =>
  requestDownload({
    defaultFileName: 'File_PrizeNoteStudents.xlsx',
    prefix,
    url: `/Student/ExportPrizeNoteStudents`,
    method: 'POST',
    data: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      semesterId,
      yearEduId,
      studentCode,
      studentFullName,
    },
  });

export const getPrizeClassifiedAmount = ({
  studentId,
  prizeClassId,
}) =>
  request({
    prefix,
    url: '/PrizeNote/PrizeClassifiedAmount',
    method: 'GET',
    params: {
      studentId,
      prizeClassId,
    },
    data: {},
  });

export const getStudentCode = id =>
  request({
    prefix,
    url: `/Student/GetByCode/${id}`,
    method: 'GET',
  });



